.nav {
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    
    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  &__link {
    position: relative;
    text-decoration: none;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: #FFFFFF;

    &::after {
      content: '';
      position: absolute;
      bottom: -4px;
      display: block;
      height: 1px;
      width: 100%;
      background-color: #FFFFFF;
      
      transition: transform 0.3s;
      transform: scale(0);
      transform-origin: left;
    }
      
    &:hover::after {
      transform: scale(1);
    }
  }
}

